import { Link } from '@reach/router'
import cx from 'classnames'
import PropTypes from 'prop-types'
import React from 'react'
import Container from '../Container/Container'
import Cta from '../Cta'
import IconClose from '../Icons/IconClose'
import IconMenu from '../Icons/IconMenu'
import './header.scss'

const SCROLL_LOCK_CLASS = 'js-scroll-lock'

const setScrollLock = () => document.body.classList.add(SCROLL_LOCK_CLASS)
const unsetScrollLock = () => document.body.classList.remove(SCROLL_LOCK_CLASS)

const Header = ({
  children,
  variant = 'default',
  centered = false,
  floating = false,
  ...props
}) => {
  return (
    <div
      className={cx('header', {
        'header--centered': centered,
        'header--floating': floating,
        [`header--${variant}`]: variant,
      })}
      {...props}
    >
      <Container type="full" className="header__container">
        <a
          href="#main-menu"
          id="main-menu-toggle"
          className="header__toggle"
          aria-label="Open main menu"
          onClick={setScrollLock}
        >
          <span className="sr-only">Open main menu</span>
          <IconMenu aria-hidden="true" />
        </a>

        {children}

        {/* eslint-disable-next-line jsx-a11y/control-has-associated-label, jsx-a11y/anchor-has-content */}
        <a
          href="#main-menu-toggle"
          className="header__backdrop"
          tabIndex="-1"
          aria-hidden="true"
          hidden
          onClick={unsetScrollLock}
        />
      </Container>
    </div>
  )
}

Header.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
  centered: PropTypes.bool,
  floating: PropTypes.bool,
}

const HeaderBrand = ({ children, to, centered = false, ...props }) => {
  return (
    <Link
      className={cx('header__brand', {
        'header__brand--centered': centered,
      })}
      to={to}
      {...props}
    >
      {children}
    </Link>
  )
}

HeaderBrand.propTypes = {
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  centered: PropTypes.bool,
}

const HeaderMenu = ({ children }) => {
  return (
    <div className="header__menu" id="main-menu">
      <a
        href="#main-menu-toggle"
        id="main-menu-close"
        className="header__close"
        aria-label="Close main menu"
        onClick={unsetScrollLock}
      >
        <span className="sr-only">Close main menu</span>
        <IconClose aria-hidden="true" />
      </a>

      <div className="header__menu-items">{children}</div>
    </div>
  )
}

HeaderMenu.propTypes = {
  children: PropTypes.node.isRequired,
}

const HeaderMenuItem = ({ active, children, to, variant }) => {
  return (
    <Cta active={active} to={to} variant={variant}>
      {children}
    </Cta>
  )
}

HeaderMenuItem.propTypes = {
  active: PropTypes.bool,
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  variant: PropTypes.string,
}

HeaderMenuItem.defaultProps = {
  variant: 'default',
}

export { Header, HeaderBrand, HeaderMenu, HeaderMenuItem }
