import PropTypes from 'prop-types'
import { Link } from '@reach/router'
import cx from 'classnames'
import React from 'react' // A.K.A. Gatsby Link
import './cta.scss'
import { Image } from '../Image'
import ArrowImageYellow from '../../static/arrow-yellow.png'

const Cta = ({ active, children, to, variant, className, ...props }) => {
  return (
    <Link
      className={cx(`cta ${className ? className : ''}`, {
        [`cta--active`]: active,
        [`cta--${variant}`]: variant,
      })}
      to={to}
      {...props}
    >
      <span className="cta__wrapper">
        {children}
        {variant === 'readmore-yellow' && (
          <Image variant="cta" src={ArrowImageYellow} alt="right arrow" />
        )}
      </span>
    </Link>
  )
}

Cta.propTypes = {
  active: PropTypes.bool,
  children: PropTypes.node.isRequired,
  className: PropTypes.string,
  to: PropTypes.string.isRequired,
  variant: PropTypes.string,
}

Cta.defaultProps = {
  variant: 'primary',
}

export default Cta
