import PropTypes from 'prop-types'
import React from 'react'
import {
  Header,
  HeaderBrand,
  HeaderMenu,
  HeaderMenuItem,
} from '../../components/Header'
import { Image } from '../../components/Image'
import LogoWeKnowIncColor from '../../static/logo-weknowinc-color.png'
import LogoWeKnowInc from '../../static/logo-weknowinc.png'

function HeaderBlock({ floating = false, menuItems, variant = 'default' }) {
  return (
    <Header variant={variant} floating={floating}>
      <HeaderBrand to="/">
        <Image
          src={LogoWeKnowIncColor}
          alt="weKnow Brand"
          className="desktop"
        />
        <Image src={LogoWeKnowInc} alt="weKnow Brand" className="mobile" />
      </HeaderBrand>

      <HeaderMenu>
        {menuItems &&
          menuItems.map((item) => (
            <HeaderMenuItem
              active={item.active}
              key={item.url}
              to={item.url}
              variant={item.highlight && 'highlight'}
            >
              {item.text}
            </HeaderMenuItem>
          ))}
      </HeaderMenu>
    </Header>
  )
}

HeaderBlock.propTypes = {
  menuItems: PropTypes.array,
  variant: PropTypes.string,
  centered: PropTypes.bool,
  floating: PropTypes.bool,
}

export default HeaderBlock
