import PropTypes from 'prop-types'
import React from 'react';
import cx from "classnames";
import "./title.scss";

function Title({ children, variant, className, ...props }) {
  return (
    <h2 className={cx(`title ${className ? className : ''}`, {
      [`title--${variant}`]: variant,
    })} {...props}>
      {children}
    </h2>
  )
}

Title.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
  className: PropTypes.string,
};

Title.defaultProps = {
  variant: 'default',
};

function TitleAccent({ children, variant }) {
  return (
    <span className={cx("title__accent", {
      [`title__accent--${variant}`]: variant,
    })}>
      {children}
    </span>
  )
}

TitleAccent.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
};

TitleAccent.defaultProps = {
  variant: 'default',
};

function TitleAccentLine({ children, variant }) {
  return (
    <span className={cx("title__accent-line", {
      [`title__accent-line--${variant}`]: variant,
    })}>
      {children}
    </span>
  )
}

TitleAccentLine.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
};

TitleAccentLine.defaultProps = {
  variant: 'default',
};

export { Title, TitleAccent, TitleAccentLine }
