import PropTypes from 'prop-types'
import React from 'react';
import cx from "classnames";
import "./image.scss";

function Image({ variant, alt, className, ...props }) {
  return <img
    alt={alt}
    className={cx(`image ${className ? className : ''}`, {
      [`image--${variant}`]: variant,
    })}
    {...props} />
}

Image.propTypes = {
  alt: PropTypes.string,
  variant: PropTypes.string,
  className: PropTypes.string,
};

function BackgroundImage({ children, image, className, ...props }) {
  return (
    <div
      className={`image--background ${className ? className : ''}`}
      style={{ backgroundImage: `url(${image})` }}
      {...props}>
      {children}
    </div>
  )
}

BackgroundImage.propTypes = {
  children: PropTypes.node.isRequired,
  image: PropTypes.node.isRequired,
  className: PropTypes.string,
};

export { Image, BackgroundImage }
